body {
  background-color: rgba(133, 135, 241, 0.151);
  color:rgb(36, 46, 16)
}
.App {
  text-align: center;
  margin: 0;
  padding: 0;
  font-family: 'Noto Sans JP', sans-serif;
}
.searchBlock {
  display: flex;
  font-size: 30px;
  justify-content: space-around;
  margin: 30px 10%;
  box-shadow: 1px 1px 5px grey;
  padding: 20px;
  background-color: rgba(250, 250, 250, 0.5);
  input {
    width: 70%;
    font-size: 20px;
    padding: 8px 30px;
    outline: none;
    border-radius: 6px;
    border: 1px solid rgb(136, 129, 129);
  }
  button {
    padding: 12px 30px;
    font-size: 20px;
    border: 1px solid grey;
    border-radius: 8px;
  }
}

.gallery {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
}
.item {
  width: 30%;
  margin-bottom: 20px;
}
.pagination {
  display: flex;
  justify-content: center;
  margin-bottom: 30px;
}

.overlay {
  position: fixed;
  width: 100%;
  height: 100%;
  background-color: rgba(31, 27, 27, 0.5);
  z-index: 1;
  top: 0;
  left: 0;
}
.wrapper {
  position: relative;
  display: inline-block;
}
.removeBtn {
  position: absolute;
  right: 0;

  &:hover {
    color: #4d4141;
    cursor: pointer;
  }
}
h1 {
  cursor: pointer;
  font-size: 40px;
}
